<template>
  <NavBar @language-changed="changeLanguage"
  :Home="texts.NavBar.Home"
  :AboutMe="texts.NavBar.AboutMe"
  :MyProjects="texts.NavBar.MyProjects"
  :MySkills="texts.NavBar.MySkills"
  :Contacts="texts.NavBar.Contacts"
  ></NavBar>
  <header>
    <div class="container">
      <div class="row">

        <section class="col-xl-3"><br></section>
        <section class="col-xl-6"><TittleName></TittleName></section>
        <section class="col-xl-3"><br></section>
      </div>
    </div>
  </header>
</template>


<script>
import es from './assets/lenguajes/es.json'
import en from './assets/lenguajes/en.json'
import TittleName from './components/Tittlename.vue';
import NavBar from './components/NavBar.vue';

export default {
  name: 'App',
  components: {
    TittleName,
    NavBar
  },
  data() {
    return {
      texts: es // Inicialmente en español
    };
  },
  methods: {
    changeLanguage(isChecked) {
      // Cambiar el texto según el estado del checkbox
      this.texts = isChecked ? en : es;
    }
  }
}
</script>

